@import url(https://fonts.googleapis.com/css?family=Lato:400,700,900);
@keyframes reveal {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fromLeft {
  0% {
    opacity: 0;
    transform: translate(-25%, 0); }
  100% {
    opacity: 1;
    transform: translate(0, 0); } }

@keyframes fromRight {
  0% {
    opacity: 0;
    transform: translate(25%, 0); }
  100% {
    opacity: 1;
    transform: translate(0, 0); } }

@keyframes sweep {
  0% {
    left: -100%; }
  100% {
    left: 100%; } }

html {
  background: #1c1f1e; }

body {
  background: #fff; }

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

img {
  height: auto;
  width: 100%;
  min-width: 100%;
  vertical-align: bottom; }

*:focus {
  border: 0;
  outline: 0; }

body {
  font-size: 100%;
  text-rendering: optimizeLegibility;
  font-family: 'Lato', sans-serif; }

@media screen {
  a {
    color: #1c1f1e; }
  p {
    font-size: 1rem;
    line-height: 1.8; }
    p a {
      font-weight: 700; }
  h1 {
    margin: 0;
    font-size: 2rem;
    font-weight: 900;
    text-transform: uppercase; }
    h1 a {
      position: relative;
      text-decoration: none;
      display: inline-flex;
      overflow: hidden; }
      h1 a:before {
        content: '';
        height: 0.25em;
        width: 100%;
        background: transparent;
        position: absolute;
        bottom: 0;
        left: -100%;
        transition: all 0.15s ease-out; }
      h1 a:after {
        content: '';
        height: 0.25em;
        width: 100%;
        background: transparent;
        position: absolute;
        bottom: 0;
        right: -100%;
        transition: all 0.15s ease-out; }
      h1 a:hover:before {
        left: 0;
        background: #1c1f1e;
        transition: all 0.05s ease-out; }
      h1 a:hover:after {
        right: 0;
        background: #1c1f1e;
        transition: all 0.05s ease-out; } }

