@media screen {
  #preloader {
    background: #ffffff;
    height: 100%; }
  .ajax-progress-fullscreen {
    animation: reveal 1s ease-in-out;
    background: transparent;
    height: auto;
    opacity: 1; }
  #preloader,
  .ajax-progress-fullscreen {
    top: 0;
    left: 0;
    border-radius: 0;
    position: fixed;
    width: 100%;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    #preloader:after,
    .ajax-progress-fullscreen:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 5px;
      background: #3c81fd;
      box-shadow: 0 0 50px rgba(255, 255, 255, 0.5);
      animation: 1s infinite sweep; } }

@media screen {
  #header {
    overflow: hidden;
    background: #3c81fd; }
    #header #profile {
      z-index: 5;
      position: relative; }
      #header #profile #logo {
        position: absolute;
        top: 15%;
        left: 15%;
        width: 70%;
        height: 70%; }
    #header #intro {
      padding: 3rem 1.5rem;
      z-index: 10;
      position: relative;
      background: #ffffff;
      display: flex;
      justify-content: center;
      align-items: center; } }

@media screen and (min-width: 550px) {
