@media screen {
  .slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent; }
  .slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0; }
    .slick-list:focus {
      outline: none; }
    .slick-list.dragging {
      cursor: pointer;
      cursor: hand; }
  .slick-slider .slick-track,
  .slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0); }
  .slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block; }
    .slick-track:before, .slick-track:after {
      content: "";
      display: table; }
    .slick-track:after {
      clear: both; }
    .slick-loading .slick-track {
      visibility: hidden; }
  .slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    display: none; }
    [dir="rtl"] .slick-slide {
      float: right; }
    .slick-slide img {
      display: block; }
    .slick-slide.slick-loading img {
      display: none; }
    .slick-slide.dragging img {
      pointer-events: none; }
    .slick-initialized .slick-slide {
      display: block; }
    .slick-loading .slick-slide {
      visibility: hidden; }
    .slick-vertical .slick-slide {
      display: block;
      height: auto;
      border: 1px solid transparent; }
  .slick-arrow.slick-hidden {
    display: none; }
  .slick-slide {
    padding: 0 0 0 1px; }
  button.slick-arrow {
    opacity: 0;
    border: 0;
    outline: 0;
    text-indent: -9999px;
    position: absolute;
    top: calc(50% - 20px);
    width: 40px;
    height: 40px;
    z-index: 15;
    background: url(../images/arrow.svg);
    transition: all 0.15s ease-out; }
    button.slick-arrow.slick-prev {
      left: -80px;
      transform: rotate(90deg); }
    button.slick-arrow.slick-next {
      right: -80px;
      transform: rotate(-90deg); }
    button.slick-arrow.slick-disabled {
      display: none !important; }
  .slick-dots {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2em 0 0; }
    .slick-dots li {
      cursor: pointer;
      width: 12px;
      height: 12px;
      margin: 0 12px;
      border-radius: 50%;
      background: #ffffff;
      opacity: .5; }
      .slick-dots li.slick-active {
        opacity: 1; }
    .slick-dots button {
      display: none; }
  .panel:hover button.slick-arrow {
    opacity: 1;
    transition: all 0.05s ease-out; } }
